body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
    background-color: white;
}
#background {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 50%;
    height: 100%;
    background-color: black;
    z-index: 1;
}

#content {
    z-index: 2;
    padding: 30px;
    text-align: center;
    font-weight: bold;
    font-family: Noto Sans TC;
    width: 800px;
    height: 400px;
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
.column1 {
    width: 50%;
   }
#github {
    color: white;
    font-size: 40px;
    position: absolute;
    bottom: 0;
    z-index: 1;
    padding-bottom: 50px;
    -webkit-transform: translateX(-90%);
            transform: translateX(-90%);
}
#linkedin {
    color: black;
    font-size: 40px;
    position: absolute;
    bottom: 0;
    padding-bottom: 50px;

}
img {
    height: 100%;
    width: 75%;
    vertical-align: middle;
}
h2 {
    color: black;
    font-family: Noto Sans TC;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}
p {
    color: black;
    font-family: Noto Sans TC;
    font-size: 15px;
    padding: 15px;
}
.column2 {
    width: 50%;
}
.icons a {
   display: inline-block;
   text-align: center;
   margin: 40px;
}


.menu {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    /* position: fixed; */
    top: 0;
    /* padding-right: 20px; */
}
.menu li:first-child {
    margin-right: auto;
    font-family: Noto Sans TC;
    z-index: 3;
  }
.menu li:last-child {
      padding-right: 40px;
  }
.menu .logo{
      color: white;
      font-size: 40px;
      font-family: 'Satisfy', cursive;
  }
.menu a {
    margin: 10px;
    display: block;
    padding: 10px;
    text-decoration: none;
    font-family: Noto Sans TC;
    font-size: 25px;
    color: black;
  }
.menu a:hover {
    border-bottom: 1px solid black;
}
.menu a:active {
    border-bottom: 1px solid black;
}
